*{
  margin: 0;
  padding: 0;
}

/* Navbar styles */
.navmain{
  position: sticky;
  top: 0;
}

.icon{
  display: none;
}

.employeeNav{
  display: flex;
  justify-content: flex-end;
  gap: 0.9rem;
  padding: 10px;
  background-color: cadetblue;
}

.employeeNav a{
  color: white;
  text-decoration : none;
}

.employeeNav a:hover{
  text-decoration: underline;
}

.navOptions{
  display: flex;
  justify-content: space-around;
  background-color: white;
}

.navLogo h1{
  color: black;
  font-family: cursive;
}

.navNavigationoptions{
  display: flex;
  gap: 0.3rem;
  padding: 25px;
}

.navNavigationoptions a{
  padding: 7px;
  text-decoration: none;
  color: black;
}

.navNavigationoptions a:hover, .navNavigationoptions a.active{
  background-color: cadetblue;
  color: white;
  border-radius: 5px;
}


/* Home page styles */
.homemain{
  background-color: cadetblue;
}

.homeServices{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 30px;
  gap: 0.8rem;
}

.homechild{
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
  color: white;
}

.homechild a{
  text-decoration: none;
  color: white;
}

.homechild a:hover{
  text-decoration: underline;
}

.footer{
  background-color: black;
  color: lightgray;
  padding: 8px;
}

/* About us page */
.aboutus, 
.services, 
.clients, 
.contactus, 
.employeelogin, 
.ad, 
.ccs, 
.training
{
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  gap: 1rem;
}

.aboutusNav, 
.servicesNav, 
.clientsNav, 
.contactusNav, 
.employeeloginNav, 
.adNav, 
.ccsNav, 
.trainingNav
{
  display: flex;
  height: 150px;
}

.aboutusNav h2,
.servicesNav h2, 
.clientsNav h2, 
.employeeloginNav h2, 
.contactusNav h2,
.adNav h2, 
.ccsNav h2, 
.trainingNav h2
{
  text-align: center;
  background-color: cadetblue;
  flex: 20%;
  padding: 80px;
  color: white;
}

.aboutusBody, 
.servicesBody, 
.clientsBody, 
.adBody, 
.ccsBody, 
.trainingBody
{
  display: flex;
  gap: 0.8rem;
  padding: 40px;
}

.aboutusBody img, 
.servicesBody img, 
.adBody img, 
.ccsBody img, 
.trainingBody img
{
  width: 500px;
  height: 280px;
  box-shadow: 10px 10px 30px grey;
}

/* services */

.servicesNav img, 
.clientsNav img, 
.contactusNav img,
.adNav img, 
.ccsNav img, 
.trainingNav img
{
  height: 160px;
}

.servicesBody ul, .clientsBody ul, .trainul ul{
  list-style: none;
}

.services a{
  text-decoration: none;
}

/* Login page */

.employeeBody{
  text-align: center;
  padding: 40px;
}

.employeeBody h3{
  padding: 10px;
}

.employeeBody form{
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 0.5rem;
  padding: 10px;
}

.employeeBody form input{
  height: 30px;
}

/* button group application development css */
.btn-group button {
  border: none; 
  color: white;
  padding: 10px 24px; 
  cursor: pointer; 
  float: left; 
}

.btn-group button:last-child{
  width: 450px;
}

.adBtngroup{
  display: flex;
  flex-direction: column;
  gap: 0.6rem;
  padding: 40px;
}

.adBtngroup p{
  width: 500px;
}

.adBtngroup p ul{
  padding-left: 30px;
}

/* Training css */
.trainul{
  padding: 40px;
}

.trainul span, .touch-h2 span{
  color: cadetblue;
}

/* contactus css */
.contactusBody{
  display: flex;
  justify-content: space-evenly;
  gap: 0.8rem;
  padding: 40px;
}

.contact-touch{
  display: flex;
  flex-direction: column;
  padding: 0px 80px;
  gap: 0.8rem;
}

.contact-touch div{
  display: flex;
  justify-content: space-between;
}

.contact-touch div input{
  width: 350px;
  height: 40px;
}

.contact-touch textarea, .contact-touch div input{
  border: 1px solid lightgray;
  padding: 0px 4px;
  border-radius: 5px;
}

.contact-touch button{
  width: 250px;
  height: 40px;
  background-color: cadetblue;
  border: none;
  color: white;
  border-radius: 5px;
  font-size: large;
}

.touch-h2{
  padding-left: 60px;
}

/* media queries */
@media only screen and (max-width:900px) {
  .aboutusBody img, .servicesBody img, .adBody img, .ccsBody img, .trainingBody img{
    width: 350px;
    height: 180px;
    box-shadow: 8px 8px 20px grey;

  }

  .aboutusNav img, 
  .servicesNav img, 
  .clientsNav img, 
  .contactusNav img,
  .employeeloginNav img, 
  .adNav img, 
  .ccsNav img, 
  .trainingNav img
  {
    display: none;
  }
  
  .aboutusBody, 
  .servicesBody, 
  .clientsBody, 
  .contactusBody,
  .adBody, 
  .ccsBody, 
  .trainingBody
  {
    flex-direction: column;
  }

  .contact-touch div{
    display: flex;
    gap: 0.6rem;
  }
  
}

@media only screen and (max-width:450px){
  .aboutusBody img, .servicesBody img, .adBody img, .ccsBody img, .trainingBody img{
    width: 250px;
    height: 140px;
    box-shadow: 5px 5px 15px grey;
  }

  .homeServices{
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }

  .icon{
    display: block;
    padding: 6px;
    margin: 10px;
    background-color: grey;
  }

  .navNavigationoptions{
    display: none;
  }

  .navOptions{
    display: flex;
    flex-direction: column;
  }

  .navLogo{
    display: flex;
    justify-content: space-evenly;
  }

  /* .navNavigationoptions{
    padding: 0;
    gap: 0;
    background-color: cadetblue;
  }

  .navNavigationoptions a{
    color: white;
  }

  .navNavigationoptions a:hover, .navNavigationoptions a:focus{
    background-color: white;
    color: black;
  } */

  .contact-touch div{
    display: flex;
    flex-direction: column;
    gap: 0.6rem;
  }

  .contact-touch div input{
    width: auto;
    height: 40px;
  }

  .contact-touch{
    padding: 10px;
  }

  .btn-group{
    display: flex;
  }

  .btn-group button:last-child{
    width: auto;
  }

  .adBtngroup p{
    width: auto;
  }
}